const parameters = {
  // Time in second for one cycle
  cycleLength: 15,

  // Time in second for the selection phase
  selectionLength: 10,

  // Maximal number of cycle in one game
  cycleCount: 20,

  // Number of villager in the vilage
  villagerCount: 6 * 4,
};

export default parameters;



